@import '../../../mixin.scss';
.about {
  display: flex;
  flex-grow: 1;
  // background: #1C1D25;
  &__container {
    display: flex;
    flex-grow: 1;
    justify-content: center;
    max-width: 1050px;
    margin: 0 auto;
    padding: 0 15px;
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    row-gap: 30px;
    @include adaptiveValue('row-gap', 30, 25, 630, 388, 1);
    @media (max-height: 674px) {
      row-gap: 15px;
    }
  }
  &__title {
    opacity: 0;
    display: flex;
    align-self: center;
    justify-content: center;
    animation: animate_Title_Link 0.7s ease 1 1.1s forwards;
    font-size: 50px;
    font-weight: 700;
  }
  &__text_container {
    position: relative;
    width: 600px;
    @include adaptiveValue('width', 600, 250, 630, 280, 1);
  }
  &__invitation_block {
    // блок приглашение нанять на работу
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 15px;
    margin: 0px 0px 20px 0px;
    @include adaptiveValue('row-gap', 15, 10, 630, 320, 1);
  }
  &__invitation_button {
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none;
    opacity: 0;
    color: black;
    display: inline-block;
    font-weight: 700;
    padding: 9px 15px 6px;
    border-radius: 30px;
    background: linear-gradient(to bottom, #f9c847, #f9c64782);
    transition: all linear 0.05s;
    cursor: pointer;
    transform: translate3d(0, 0, 0);
    &:active {
      transform: translate3d(0, 0, 0);
      box-shadow: #f9c64752 0px 16px 15px -2px;
      transform: translate(0px, 2px);
    }
  }
}
.about__text_substrate {
  // текст подложка
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
  opacity: 0;
  position: relative;
  display: inline;
  font-size: 23px;
  line-height: 35px;
  @include adaptiveValue('line-height', 35, 22, 630, 388, 1);
  @include adaptiveValue('font-size', 23, 22, 630, 388, 1);
  @media (min-width: 536px) and (max-height: 736px) {
    line-height: 30px;
  }
  @media (min-width: 536px) and (max-height: 674px) {
    line-height: 25px;
    font-size: 22px;
  }
  @media (max-width: 361px) and (max-height: 681px) {
    font-size: 20px;
  }
}
.animationIsLoading {
  transform: translate3d(0, 0, 0);
  box-shadow: #f9c64752 0px 20px 15px 0px;
  animation: animate_Title_Link 0.7s ease 1.1s forwards;
}
.animationIsLoaded {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  box-shadow: #f9c64752 0px 20px 15px 0px;
}
@keyframes animate_Title_Link {
  0% {
    transform: translate(0, 20px);
  }
  100% {
    transform: translate(0px, 0px);
    opacity: 1;
  }
}
